<template>
  <div class="count">
    <div class="countTime" >
        <div class="countImg">
           <img src="../assets/img/turn.gif" alt=""> 
        </div>
        <div class="countCenter">
            距离 <span v-text="holiday" class="holiday"></span> 还有 <span v-text="holidayTime"></span>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "countDown",
  data() {
    return {
      holidayList: ["2018-12-25", "2018-12-30", "2019-02-04", "2019-02-19", "2019-04-05", "2019-05-01", "2019-06-07", "2019-09-13", "2019-10-01"],
      holidayTitle: ["圣诞节", "元旦节", "春节", "元宵节", "清明节", "劳动节", "端午节", "中秋节", "国庆节"],
      holiday: "",
      holidayTime: "",
      _timer: "",
      timstr: '',
    };
  },
  beforeRouteLeave(to, from, next) {
    // 设置下一个路由的 meta
    to.meta.keepAlive = false;
  },  
  methods: {
    //  倒计时调用
    timerfunc() {
        const _that = this;
        this._timer = setInterval(_that.tfunc, 1000);
    },
    //  倒计时处理
    tfunc() {
      const timstData = this.holidayList,
        timstTitle = this.holidayTitle;
      for (const i in timstData) {
        if (new Date(timstData[i]).valueOf() > new Date().valueOf()) {
          this.timstr = timstData[i];
          this.holiday = timstTitle[i];
          break;
        }else if(new Date(timstData[i]).valueOf() == new Date().valueOf()){
          console.log(111)
          break;
        }
      }
      const ntim = new Date(); //当前时间戳
      const _tm = this.timstr.replace(/-/g, "/"); //目标时间字段串里的-替换成/,格式化的需要
      const ftim = new Date(_tm); //格式化目标时间
      const rs = this.timGap(ntim.getTime(), ftim.getTime()); //调用取时间差方法
      const _str = (rs.d ? rs.d + "天" : "") +
        (rs.h ? rs.h + "小时" : "") +
        (rs.m.toString()[1] ? rs.m + "分钟" : "0"+rs.m + "分钟") +
        (rs.s.toString()[1] ? rs.s + "秒" : "0"+rs.s + "秒"); //将返回的数据拼接字符串
      this.holidayTime = _str; //输出
    },
    //取时间差方法
    timGap(ntim, ftim) {
      const date3 = ftim - ntim; //时间差值毫秒数

      const days = Math.floor(date3 / (24 * 3600 * 1000)); //取天数

      const level1 = date3 % (24 * 3600 * 1000); //取天数后剩下的毫秒数
      const hours = Math.floor(level1 / (3600 * 1000)); //取小时数

      const level2 = level1 % (3600 * 1000); //取小时后剩下的毫秒数
      const minutes = Math.floor(level2 / (60 * 1000)); //取分钟

      const level3 = level2 % (60 * 1000); //取分钟后剩下的毫秒数
      const seconds = Math.floor(level3 / 1000); //取秒

      //定义对象
      const tim = {};
      //赋值
      tim["d"] = days;
      tim["h"] = hours;
      tim["m"] = minutes;
      tim["s"] = seconds;
      //console.log(tim);
      return tim; //返回数据
    }
  },
  mounted: function() {
    this.timerfunc();
  }
};
</script>

<style lang="less" scoped>
.count {
  width: 100%;
  height: 100%;
    .countTime{
      width: 80%;
      height: 80%;
      margin: 5% auto;
    }
    .countCenter{
        width: 100%;
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-size: 16px;
        .holiday{
            color: red;
        }
    }
}
</style>






